.Testimonial{
  /* margin-top: 100px; */
  padding-top: 100px;
}

.Testimonial .heading h1{
  @media screen and (max-width:768px) {
    
    font-size: 3rem;
  }
}

.Testimonial h2 {
  margin: 15px 0 0 0;
}



.Testimonial .quote i {
  font-size: 150px;
  opacity: 0.13;

  @media screen and (max-width:1200px) {
    font-size:80px;
  }
}

.Testimonial .content {
  padding: 25px;
}

.Testimonial .content h1 {
  font-weight: 500;
}


.Testimonial .content h3 {
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 40px;
}

.Testimonial .slide_button {
  position: absolute;
  top: 30px;
  right: 20px;
  display: flex;
  gap: 10px;
  

  @media screen and (max-width:768px) {
    top: 380px;
    right:65px;
  }
}

.Testimonial .slide_button .prev_btn {
  margin-right: 30px;
  display: flex;
  /* width: 10px; */
  /* height: 10px; */
  justify-content: center;
  align-items: center;
 
 
  
  
}

/*-----Testimonial slide-----------*/
.Testimonial .slide {
  width: 80%;
  margin: auto;
  height: 750px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  

  @media screen and (max-width:1400px) {
    width:95%;
    
  }
}

.slide .left{  
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 420px;
  height: 500px;

  @media screen and (max-width:1200px) {
    width:350px;
    height:400px
  }
}


.slide img{
  width: 350px;
  height: 300px;
  /* background-color: red; */
  object-fit: cover;

  @media screen and (max-width:1200px) {
    width:300px;
    height:240px
  }
}

.slide .right{
  width: 600px;
  /* height: 700px; */
  display: flex;
  flex-direction: column;

  @media screen and (max-width:1200px) {
    width:550px;
  
  }
}

article {
  position: absolute;
  top: 0;
  left: 0;
  width: 99%;
  height: 100%;
  opacity: 0;
  transition: all 0.3s linear;
}

article.activeSlide {
  opacity: 1;
  transform: translateX(0);
}
article.lastSlide {
  transform: translateX(-100%);
}
article.nextSlide {
  transform: translateX(100%);
}
/*-----Testimonial slide-----------*/

@media (max-width:1024px) {
  
  article {
  flex-direction: column;
 
  }

  .Testimonial .left {
    width: 100%;
    height: 280px;
    flex-direction: row;
    gap: 20px;
    justify-content: space-evenly;
    margin: 0;
    margin-top: 15%;
  }

  .Testimonial .left img {
    width: 300px;
    height: 200px;
    border-radius: 10px;
  }

  .Testimonial .right {
    width: 100%;
    margin: 0;
    padding: 0 0 0 0px;
    height: 700px;
  }

  .Testimonial .slide {
    height: 1000px;
    max-width: 90%;
  }

  .Testimonial .quote {
    display: none;
  }
}

@media (max-width:768px) {
  article {

    gap: 50px;
    }
}

@media (max-width:768px) {

  .Testimonial .slide {
    /* height: 100vh; */
    /* height: 1100px; */
    max-width: 90%;
  }


  .Testimonial .left {
    flex-direction: column;
    height: 400px;
    justify-content: center;
    /* align-items: center; */
    gap: 0px;
    padding: 0;
    margin: 0;
  }

  .Testimonial .right {
    width: 100%;
    height: 70%;
   
  }
}