.features{
  padding-top: 60px;
  padding-left: 30px;
    padding-right: 30px;

  @media screen and (max-width:768px) {
    padding-top: 70px;
    padding-left: 10px;
    padding-right: 10px;
  }
}


.features .heading{

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.features .content{
  display: flex;
  gap: 50px;
  justify-content: center;
  flex-wrap: wrap;
}

.features .box {
  padding: 50px;
  width: 550px;

  @media screen and (max-width:1280px) {
    width:320px
  }

}

.features .box h2 {
  margin: 20px 0 20px 0;
  
}
.features .box p {
  word-spacing: 2px;
  transition: 0.5s;
}

.features .box img{
  width: 50px;
  height:50px;
}
.features .box i {
  
  font-size: 30px;
  margin-top: 20px;
  transition: 0.5s;
  opacity: 0;
}
.features .box:hover p {
  color: white;
}
.features .box:hover i {
  color: white;
  opacity: 1;
}
